import  React ,
      { useEffect,
        useState, }         from "react";
import { db }               from '../../firebase';
import { doc , 
         collection,
         getDocs ,
         updateDoc ,}       from 'firebase/firestore';
import { Typography , 
         Box ,
         Grid,
         createTheme , 
         ThemeProvider ,
         Link,}             from "@mui/material"
import { format ,}          from "date-fns"
import   useProfile         from "../hooks/useProfile"
import   Divider            from '@mui/material/Divider';
import   Paper              from '@mui/material/Paper';
import   Stack              from '@mui/material/Stack';
import { styled }           from '@mui/material/styles';
import   MainpageImgButton  from './MainPageImageButton'
import   ThumbUpAltIcon     from '@mui/icons-material/ThumbUpAlt';
import   StarsIcon          from '@mui/icons-material/Stars';
import { firebaseApp }      from "../../firebase";

const collectionRecipeName    = "recipe"
const collectionGoodName      = "/good"
const collectionBookMarkName  = "/bookmark"

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));
const theme = createTheme()

export default function MainPageImageList() {
  const [recipe, setRecipe] = useState([]);

  const profileData = useProfile()
  const profile = profileData.profile
  const recipeAry = [];

  // firestore
  const fetchRecipeData = () => {
    var goodBuffCount = 0
    var bookmarkBuffCount = 0
    const firestore = firebaseApp.firestore
    getDocs(collection(db, collectionRecipeName)).then((querySnapshot)=>{
      querySnapshot.forEach((document) => {
        // good
        const docGoodRef = doc(firestore, collectionRecipeName , document.id)
        getDocs(collection(db, collectionRecipeName+"/"+document.id+collectionGoodName)).then((querySnapshot)=>{
          // 
          goodBuffCount = 0
          querySnapshot.forEach((documentGood) => {
            goodBuffCount = goodBuffCount + 1
          })
          updateDoc(docGoodRef , {
            good : goodBuffCount,
          })
        })
        // bookmark
        getDocs(collection(db, collectionRecipeName+"/"+document.id+collectionBookMarkName)).then((querySnapshot)=>{
          // 
          bookmarkBuffCount = 0  
          querySnapshot.forEach((documentBookMark) => {
            bookmarkBuffCount = bookmarkBuffCount + 1
          })
          updateDoc(docGoodRef , {
            bookmark : bookmarkBuffCount,
          })
        })
        recipeAry.push({
          id : document.id,
          good : 0,
          bookmark : 0,
          ...document.data(),
        })
      })
    }).then(()=>{
      console.log("recipeAry : " , recipeAry)
      setRecipe([...recipeAry])
      // (createdAt)
      recipeAry.sort(function(first , second){
        return (format(first.createdAt.toDate() , "yyyyMMdd") < format(second.createdAt.toDate() , "yyyyMMdd")) ? -1 : 1
      })
    })
  }

  useEffect(() => {
    fetchRecipeData()
  },[]);


  return (
    <ThemeProvider theme={theme}>
      <Grid item xs={12} >
        {recipe ? (
          recipe.sort().map((recipe) => (
            <Box 
              key={recipe.id} 
              sx={{
                display: "flex",
                my: 2,
                gap: 2,
                flexGrow: 1, m: 2,}}>
              <Box>
                <MainpageImgButton
                    imgURL = {recipe.image.url}
                    info   = {recipe}
                    className   = "MuiImageBackdrop-root"
                    link   = "/recipedetail/"
                    style  = {{width: "100px"}}/>
              </Box>
              <Box sx={{ ml: 2 }}>
                {/*  */}
                <Grid container spacing={0} >
                <Grid item xs= {10}>
                  <Typography sx={{ fontSize: 22}}>
                    {/*  */}
                    <Link href={`/recipedetails/${recipe.recipenum}`} color="#000000">
                      <strong>{recipe.title}</strong>
                    </Link>
                  </Typography>
                </Grid>
                <Grid item xs= {1} >
                  {/* / */}
                  <ThumbUpAltIcon sx={{ color : "#ffa500" ,fontSize: 20}}/>
                    <Typography color="#000000" variant="caption" fontSize={15}>
                      {recipe.good}
                    </Typography>
                </Grid>
                {/* / */}
                <Grid item xs= {1}>
                  <StarsIcon sx={{ color : "#a0522d" }}/>
                  <Typography color="#000000" variant="caption" fontSize={15}>
                    {recipe.bookmark}
                  </Typography>
                </Grid>
                {/* / */}
                {/*  */}
                {/* <Grid item xs= {1}>
                  <InsertCommentIcon sx={{ color : "#1e90ff" }}/>
                  <Typography color="#000000" variant="caption" fontSize={15}>
                    XXX
                  </Typography>
                </Grid> */}
                </Grid>
                {/*  */}
                <Grid>
                  <Typography 
                    sx={{ p: 1, 
                          fontSize: 14 , 
                          maxWidth : 600 ,
                          minWidth : 600 , 
                          background: "#dddddd" ,
                          color:"#000000",
                          borderRadius : 3,}}>
                    {recipe.memo}
                    <Typography
                      sx={{ fontSize: 12 ,color:"#000000" }}
                      align = "right">
                      {format(recipe.createdAt.toDate(), "yyyyMMdd")}
                    </Typography>
                  </Typography>
                </Grid>
                <Stack
                  direction="row"
                  divider={<Divider orientation="vertical" flexItem />}
                  spacing={2}>
                    {/*  */}
                  <Item 
                    sx = {{
                    minWidth : 250,
                    maxWidth : 250,}}>
                    {/*  */}
                    {/* uid */}
                    <Typography
                      sx={{ fontSize: 12 ,color:"#000000" }}
                      align = "left">
                      <Link href={`/profiles/${recipe.image.uid}`} color="#000000">
                        {recipe.image.user}
                      </Link>
                    </Typography>
                  </Item>
                  {/*  */}
                  <Item sx = {{
                    minWidth : 250,
                    maxWidth : 250,}}>
                    <Typography
                      sx={{ fontSize: 12 ,color:"#000000" }}
                      align = "left">
                      <strong>{Number(recipe.cost).toLocaleString()}</strong> 
                    </Typography>
                  </Item>
                </Stack>
              </Box>
            </Box>
          ))) : (
          <p></p>)}
      </Grid>
    </ThemeProvider>
  );
}
