import   React, 
       { useState,
         useEffect,}         from "react"
import { Box ,
         Typography,
         Container, 
         Grid, }       from '@mui/material'
import { createTheme,
         ThemeProvider }     from '@mui/material/styles'
import { useHistory,
         withRouter }        from 'react-router'
import   TermsHeader    from './TermsHeader'
import   Footer              from '../Footer'

const theme = createTheme({
  shadows: ["none"],
  palette: {
    // 
    primary: {
      main: '#E64545',
      contrastText: '#ffffff',
    },
    // 
    background: {
      default: '#ffffff',
    },
    // 
    text: { primary: '#000000' },
  },
})

function Terms(props) {
  
  return (
    <ThemeProvider theme={theme}>
        <Container maxWidth>
            <TermsHeader/>
        </Container>
        <Container maxWidth = "md" >
            <Box sx={{ flexGrow: 1, m: 2, pt: 4, pb: 4 }}>
                <Container maxWidth>
                    <Typography
                        component="h1"
                        variant="h5"
                        align="center"
                        color="text.primary"
                        gutterBottom
                        sx = {{backgroundColor : "#ffffff",
                                color : "#000000",
                                padding: "1rem 2rem",
                                pt : 5 , 
                                pb : 1 ,}}>
                        
                    </Typography>
                    {TermsData.map((item) => (
                      <Grid>
                        <Typography
                            component="h1"
                            variant="h6"
                            align="left"
                            color="text.primary"
                            gutterBottom
                            sx = {{backgroundColor : "#eeeeee",
                                    color : "#000000",
                                    padding: "1rem 2rem",
                                    borderTop    : "solid #E64545 1px",
                                    borderBottom : "solid #E64545 1px",
                                    borderLeft   : "solid #E64545 10px",
                                    borderRight  : "solid #E64545 1px",
                                    borderRadius : 2,
                                    pt : 1 , 
                                    pb : 1 ,}}>
                            {item.title}
                        </Typography>
                        <Typography
                            component="h1"
                            variant="body1"
                            align="left"
                            color="text.primary"
                            gutterBottom
                            sx = {{backgroundColor : "#ffffff",
                                    color : "#000000",
                                    padding: "1rem 2rem",
                                    pt : 1 , 
                                    pb : 1 ,}}>
                            {item.contents}
                        </Typography>
                      </Grid>
                    ))}
                </Container>
            </Box>
        </Container>
      {/* Footer */}
      <Footer/>
      {/* End footer */}
    </ThemeProvider>
  );
}

export default withRouter(Terms);

