import   React, 
      {  useEffect,
         useState }       from "react"
import { Avatar,
         Paper,
         Typography,
         CssBaseline,
         Box,
         TextField,
         Button,
         Container,
         Tabs ,
         Tab , }           from "@mui/material"
import { useHistory}       from 'react-router';
import { createTheme, 
         ThemeProvider }   from '@mui/material/styles';
import   useProfile        from "../hooks/useProfile"
import   store             from '../../store/index';
import   ProfilesImageList from "./ProfilesImageList"
import   BookmarkImageList from "./BookmarkImageList"
import   GoodImageList     from "./GoodImageList"
import   ProfilesHeader    from "./ProfilesHeader"
import { db }              from '../../firebase';
import { collection,
         getDocs ,}        from 'firebase/firestore';
import Footer            from '../Footer';

const collectionName = "users"

const theme = createTheme({
  shadows: ["none"],
  palette: {
    // 
    primary: {
      main: '#E64545',
      contrastText: '#ffffff',
    },
    // 
    background: {
      default: '#ffffff',
    },
    // 
    text: { primary: '#000000' },
  },
});

// 
function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  )
}
function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const Profiles = () => {
  const [name, setName] = useState("")
  const [location , setLocation] = useState("")
  const [userinfo, setUserInfo] = useState([]);
  const [selfintroduction , setSelfIntroduction] = useState("")
  const [image, setImage] = useState()
  const profileData = useProfile()
  const profile = profileData.profile
  const history = useHistory()
  const userDataAry = [];

  // pathnameuid
  const uidAry = window.location.pathname.split("/")
  const getuid = uidAry[2]

  const handleSubmit = (event) => {
    store.getState().displayName = profile.name
    store.getState().location    = profile.location
    store.getState().memo        = profile.selfintroduction
    history.push("/profile/edit")
  }

  const [value, setValue] = React.useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  // firestore
  const fetchUsersData = () => {
    getDocs(collection(db, collectionName)).then((querySnapshot)=>{
      // recipenumNo
      querySnapshot.forEach((doc) => {
          // 
          // String()
          if(String(doc.data().uid) === String(getuid)){
            userDataAry.push({
              id : doc.id,
              ...doc.data()
          })
        }
      })
    }).then(()=>{
      setUserInfo([...userDataAry])
    })}

  useEffect(() => {
    fetchUsersData()
  },[])

  return (
    <ThemeProvider theme={theme}>
        <Container maxWidth>
            <ProfilesHeader/>
        </Container>
        {userinfo ? ( 
          userinfo.map((userinfo) => (
        <Container maxWidth="sm">
          <CssBaseline />
          {/*  */}
          <Box sx={{ width: '100%' }}>
            <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
              <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
                <Tab label="" {...a11yProps(0)} />
                <Tab label="" {...a11yProps(1)} />
                {/*  */}
                {profile && profile.uid === getuid ? 
                  <Tab label="" {...a11yProps(2)} />
                 : ""}
                {/*  */}
                {profile && profile.uid === getuid ? 
                  <Tab label="" {...a11yProps(3)} />
                 : ""}
                </Tabs>
            </Box>
            <TabPanel value={value} index={0}>
              {/*  */}
              <Paper sx={{ m: 1, p: 1 }}>
                {/*  */}
                  <Typography align="center" variant="h5" >
                    {name ? name : userinfo ? userinfo.name : ""} <br/>
                    </Typography>
                  <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 4 }}>
                  <Container align = "center">
                    {/*  */}
                      <Avatar
                        sx={{ width: 100, height: 100 }}
                        src={image ? URL.createObjectURL(image) : userinfo ? userinfo.image : ""}alt=""/>
                    <input
                        id     = "image"
                        type   = "file"
                        accept = "image/*"
                        style  = {{ display: "none" }}/>
                  </Container>
                      <Container align="left">
                        <Typography
                          sx = {{backgroundColor : "#ffffff",
                                 color : "#000000",
                                 padding: "0.2rem",
                                 borderLeft : "solid #E64545 6px"}}>
                          <strong></strong>
                        </Typography>
                        {/*  */}
                        <Typography
                          sx = {{pt : 2 , pl : 2 ,}}>
                            {name ? name : userinfo ? userinfo.name : ""}
                        </Typography>
                      </Container>
                      <br/>
                      <Container align="left">
                      <Typography
                        sx = {{backgroundColor : "#ffffff",
                               color : "#000000",
                               padding: "0.2rem",
                               borderLeft : "solid #E64545 6px"}}>
                        <strong></strong>
                      </Typography>
                        {/*  */}
                          <Typography
                            sx = {{pt : 2 , pl : 2 ,}}>
                              {location ? location : userinfo ? userinfo.location : ""}
                          </Typography>
                        </Container>
                        <br/>
                        <Container align="left">
                          <Typography
                            sx = {{backgroundColor : "#ffffff",
                                   color : "#000000",
                                   padding: "0.2rem",
                                   borderLeft : "solid #E64545 6px"}}>
                            <strong></strong>
                          </Typography>
                          {/*  */}
                          <Typography
                            sx = {{pt : 2 , pl : 2 ,}}>
                              {selfintroduction ? selfintroduction : userinfo ? userinfo.selfintroduction : ""}
                          </Typography>
                        </Container>
                        {/*  */}
                      {profile && profile.uid === getuid ? 
                      <Button type="submit" fullWidth variant="contained" sx={{ mt: 3, mb: 2 }}>
                        
                      </Button> : ""}
                  </Box>
              </Paper>
            </TabPanel>
            <TabPanel value={value} index={1}>
              {/*  */}
              <Paper sx={{ m: 0, p: 0 }}>
                <Typography align="center" variant="h5">
                  {name ? name : userinfo ? userinfo.name : ""} <br/>
                  
                </Typography>
                <ProfilesImageList/>
              </Paper>
            </TabPanel>
            <TabPanel value={value} index={2}>
              {/*  */}
              <Paper sx={{ m: 1, p: 1 }}>
                  <Typography align="center" variant="h5">
                  {name ? name : profile ? profile.name : ""} <br/>
                    </Typography>
                    <BookmarkImageList/>
              </Paper>
            </TabPanel>
            <TabPanel value={value} index={3}>
              {/*  */}
              <Paper sx={{ m: 1, p: 1 }}>
                  <Typography align="center" variant="h5">
                  {name ? name : profile ? profile.name : ""} <br/>
                    </Typography>
                    <GoodImageList/>
              </Paper>
            </TabPanel>
          </Box>
        </Container>
        ))) : (
          <p></p>)}
      <Footer/>
    </ThemeProvider>
  )
}

export default Profiles