import { createStore }  from "redux";
import {UPDATE_FORM,
        UPDATE_RECIPE,} from '../actions/memberAction';

const initialState = {
  address          : "",
  addressErrorMS   : "",
  password1        : "",
  password2        : "",
  passwordErrorMS  : "",
  nicknameErrorMS  : "",
  location         : "",
  locationErrorMS  : "",
  loginUserUID     : "",  // UID
  displayName      : "",  // 
  recipetitle      : "",  // 
  category         : "",  // 
  detail           : "",  // 
  productionCost   : "",  // 
  productionPeriod : "",  // 
  productionMemo   : "",  // 
  createdAt        : "",  // 
  recipeimage      : "",  // 
  documentID       : "",  // firebaseID
  recipeUID        : "",  // recipe-image-uid
  commentCount     : "",  // 
  photoURL         : "",  
  memo             : "",
  photoFileData    : "",
  phoneNumber      : "",
};

const reducer = (state = initialState ,action) => {
    switch (action.type) {
        case UPDATE_FORM:
            let formState = {...state};
            formState = action.payload;
            return formState;
        case UPDATE_RECIPE:
            let recipeState = {...state};
            recipeState = action.payload;
            return recipeState;
        default:
            return state;
    }
};

const store = createStore(reducer);

export default store;