import React, 
      { useEffect,
        useState,
        useRef ,
        useLayoutEffect, } from "react";
import { db }      from '../../firebase';
import { collection,
         deleteDoc,
         doc ,
         getDocs ,
         addDoc ,}  from 'firebase/firestore';
import { Avatar ,
         Typography , 
         Box ,
         Grid,
         Container ,
         createTheme , 
         ThemeProvider ,
         CssBaseline , } from "@mui/material"
import { format ,
         formatDistance,  } from "date-fns"
import { ja } from "date-fns/locale"
import MessageInput from "./MessageInput";
import MainpageHeader    from '../MainPage/MainPageHeader';
import useProfile from "../hooks/useProfile"
import { useHistory } from "react-router-dom"

const collectionName = "message"
const collectionCategoryName = "category"
const titleName      = ""
const theme = createTheme({
  shadows: ["none"],
})

function App() {
  // 
  const [addKanji, setaddKanji] = useState("");
  const [addKana, setaddKana] = useState("");
  const [message, setMessage] = useState([]);
  const profileData = useProfile()
  const profile = profileData.profile
  const array = [];
  const bottomRef = useRef(null)
  const history = useHistory();

  useLayoutEffect(() => {
    bottomRef?.current?.scrollIntoView()
  })
  
  const time = (date) => {
    let timestamp = formatDistance(new Date(), date.toDate(), {
      locale: ja,
    })
    if (timestamp.indexOf("") !== -1) {
      return (timestamp = "")
    } else if (
      timestamp.indexOf("") !== -1 ||
      timestamp.indexOf("") !== -1
    ) {
      return (timestamp = format(date.toDate(), "yyyyMd", {
        locale: ja,
      }))
    } else {
      return (timestamp = timestamp + "")
    }
  }
  
  const fetchMessageData = () => {
    getDocs(collection(db, collectionName)).then((querySnapshot)=>{
      querySnapshot.forEach((doc) => {
        console.log(doc.id,doc.data())
        console.log(doc.data().text)
        console.log(format(doc.data().createdAt.toDate(), "yyyyMMddhh:mm"))
        array.push({
          id : doc.id,
          ...doc.data()
        })})
    }).then(()=>{
      setMessage([...array])
    })};

  useEffect(() => {
    fetchMessageData()
  },[]);

  // 
  const handleDelete = (id) => {
    console.log("id => ",id)
    if (window.confirm("")) {
      console.log("OK " , id)
      
      // id
      console.log("id")
      console.log("ID",id)
      deleteDoc(doc(db , collectionName , id)).then((doc) => {
        fetchMessageData()
        alert("")
      })
      .catch(() => {
        alert("")
     })
    }else{
      console.log("")
    }
  };

  // 
  const handleAdd = () => {
    addDoc(collection(db , collectionCategoryName),{
      category: addKanji,
      detail: addKana,
    }).then(() => {
      fetchMessageData();
      setaddKana("");
      setaddKanji("");
      alert("");
    }).catch(() => {
      alert("");
    });
  }

  const handleClick = () => {
    history.push("/test")
  }

  return (
    <ThemeProvider theme={theme}>
      <Container maxWidth >
        <MainpageHeader/>
      </Container>
      <Container maxWidth="md">
        <CssBaseline />
        <Box sx={{ flexGrow: 1, m: 2, pt: 6, pb: 4 }}>
          <Typography align="center" variant="h4">
            {titleName}
          </Typography>
        <Typography align="center">
            category:{" "}
            <input
              type="text"
              value={addKanji}
              onChange={(event) => setaddKanji(event.target.value)}
            />
            detail:{" "}
            <input
              type="text"
              value={addKana}
              onChange={(event) => setaddKana(event.target.value)}
            />
          <button onClick={() => handleAdd()}></button>
          <button onClick={() => handleClick()}>TEST</button>
        </Typography>
        <Grid item xs={12}>
            {message ? (
              message.map((message) => (
                <Box 
                  key={message.id} 
                  sx={{
                    display: "flex",
                    flexDirection:profile && profile.uid === message.user.uid ? "row" : "row-reverse",
                    my: 2,
                    gap: 2,
                    flexGrow: 1, m: 2,}}>
                  <Box>
                    <Avatar src={message.user.image ? message.user.image : ""} alt="" />
                  </Box>
                  <Box sx={{ ml: 2 }}>
                    <Typography sx={{ fontSize: 12 }}>
                      {message.user.name}
                    </Typography>
                    <Typography sx={{ p: 1, background: "#dddddd", borderRadius: 1 }}>
                      {message.text}
                      <button onClick={() => handleDelete(message.id)}></button>
                    </Typography>
                    <Grid container spacing={1}>
                      <Grid item xs={8} align = "left">
                        <Typography sx={{ fontSize: 12 }}>
                          {format(message.createdAt.toDate(), "yyyyMMddhh:mm")}
                        </Typography>
                      </Grid>
                      <Grid item xs={4} align = "right">
                        <Typography sx={{ fontSize: 12 }}>
                          {time(message.createdAt)}
                        </Typography>
                      </Grid>
                    </Grid>
                  </Box>
                </Box>
              ))) : (
              <p></p>)}
          </Grid>
        </Box>
      </Container>
      <MessageInput/>
    </ThemeProvider>
  );
}

export default App;