import React, 
     { useEffect,
       useState }          from "react"
import {Alert , 
        Avatar,
        Box,
        Grid,
        Typography,
        Container,
        CssBaseline,
        Link, 
        Button,
        IconButton,
        Tooltip , }        from "@mui/material"
import { createTheme, 
         ThemeProvider }   from '@mui/material/styles';
import RecipeDetailsHeader from "./RecipeDetailsHeader"
import RecipeDetailsImageModule from "./RecipeDetailsImageModule"
import RecipeDetailsImageButton from "./RecipeDetailsImageButton"
import Footer              from '../Footer';
import ThumbUpAltIcon      from '@mui/icons-material/ThumbUpAlt';
import CardMedia           from '@mui/material/CardMedia';
import useProfile          from "../hooks/useProfile"
import { firebaseApp }     from "../../firebase";
import { useHistory }      from 'react-router';
import { db }              from '../../firebase';
import { collection,
         getDocs ,
         addDoc ,
         Timestamp ,
         doc ,
         deleteDoc ,}      from 'firebase/firestore';
import { format }          from "date-fns"
import InsertCommentIcon   from '@mui/icons-material/InsertComment';
import store               from '../../store/index';
import Message             from "./RecipeDetailsMessage";
import StarsIcon           from '@mui/icons-material/Stars';

const collectionRecipeName    = "recipe"
const collectionUserName      = "users"
const collectionMessageName   = "message"
const collectionBookMarkName  = "/bookmark"
const collectionGoodName      = "/good"

const theme = createTheme({
  shadows: ["none"],
  palette: {
    // 
    primary: {
      main: '#E64545',
      contrastText: '#ffffff',
    },
    // 
    background: {
      default: '#ffffff',
    },
    // 
    text: { 
      primary: '#000000' },
    },
});

export default function RecipDetail() {
  const [goodadd , setGoodAdd] = useState("")
  const [goodcansel , setGoodCansel] = useState("")
  const [goodflg , setGoodFlg] = useState(false)
  const [bookmarkadd , setBookMarkAdd] = useState("")
  const [bookmarkcansel , setBookMarkCansel] = useState("")
  const [bookmarkflg , setBookMarkFlg] = useState(false)
  const [recipe, setRecipe] = useState([])
  const [userinfo, setUserInfo] = useState([])
  const [message, setMessage] = useState([])
  const [getuid , setGetUID] = useState("")
  const [getavatarurl , setGetAvatarURL] = useState("")
  const profileData = useProfile()
  const profile = profileData.profile
  const firestorage = firebaseApp.firestorage
  const history = useHistory()
  const recipeAry = [];
  const userDataAry = [];
  const messageAry = [];
  const [commentcount , setCommentCount] = useState("")

  // pathnameNo
  var recipenumAry = window.location.pathname.split("/")
  const getrecipenum = recipenumAry[2]

  // firestore
  const fetchRecipeData = () => {
    var goodBuffCount = 0
    var bookarkBuffCount = 0
    var goodFlg = 0
    var bookmarkFlg = 0
    getDocs(collection(db, collectionRecipeName)).then((querySnapshot)=>{
      // recipenumNo
      querySnapshot.forEach((doc) => {
        // 
        // String()
        if(String(doc.data().recipenum) === String(getrecipenum)){
          recipeAry.push({
            id : doc.id,
            ...doc.data()
          })
          setGetUID(doc.data().image.uid)
          store.getState().documentID       = doc.id               // ID
          store.getState().recipeUID        = doc.data().image.uid // UID
          store.getState().recipetitle      = doc.data().title     // 
          store.getState().category         = doc.data().category  // (category)
          store.getState().detail           = doc.data().detail    // (detail)
          store.getState().productionCost   = doc.data().cost      // 
          store.getState().productionPeriod = doc.data().period    // 
          store.getState().productionMemo   = doc.data().memo      // 
          store.getState().createdAt        = doc.data().createdAt // 
          store.getState().displayName      = doc.data().image.user// 
          store.getState().recipeimage      = doc.data().image.url // 
          // good
          getDocs(collection(db, collectionRecipeName+"/"+doc.id+collectionGoodName)).then((querySnapshot)=>{
            querySnapshot.forEach((doc) => {
              goodBuffCount = goodBuffCount +1
              // good
              // 
              if(String(doc.data().uid)===String(store.getState().loginUserUID)){
                console.log("good")
                console.log(doc.data().uid , store.getState().loginUserUID)
                goodFlg = goodFlg + 1 
              }else{
                console.log("good")
                console.log(doc.data().uid , store.getState().loginUserUID)
              }
            })
            // goodFlg0goodUID
            if(goodFlg > 0){
              setGoodFlg(true)
            }else{
              setGoodFlg(false)
            }
            setGoodCount(goodBuffCount)
          })
          // bookmark
          getDocs(collection(db, collectionRecipeName+"/"+doc.id+collectionBookMarkName)).then((querySnapshot)=>{
            querySnapshot.forEach((doc) => {
              bookarkBuffCount = bookarkBuffCount +1
              // bookmark
              // 
              if(String(doc.data().uid)===String(store.getState().loginUserUID)){
                console.log("bookmark")
                console.log(doc.data().uid , store.getState().loginUserUID)
                bookmarkFlg = bookmarkFlg + 1 
              }else{
                console.log("bookmark")
                console.log(doc.data().uid , store.getState().loginUserUID)
              }
            })
            // bookmarkFlg0bookmarkUID
            if(bookmarkFlg > 0){
              setBookMarkFlg(true)
            }else{
              setBookMarkFlg(false)
            }
            setBookMarkCount(bookarkBuffCount)
          })
      }else{
    }})
    }).then(()=>{
      setRecipe([...recipeAry])
    })};

  // getState
  const handleClick = () => {
    store.getState().photoURL    = getavatarurl      // URL
    history.push(`/recipedetails/${getrecipenum}/edit`)
  }

  // firestore
  const fetchUsersData = () => {
    getDocs(collection(db, collectionUserName)).then((querySnapshot)=>{
      // recipenumNo
      querySnapshot.forEach((doc) => {
        // 
        // String()  
        if(String(doc.data().uid) === String(getuid)){
          userDataAry.push({
            id : doc.id,
            ...doc.data()
        })
        setGetAvatarURL(doc.data().image)
      }
    })
    }).then(()=>{
      setUserInfo([...userDataAry])
    })};

  // firebase
  const fetchMessageData = () => {
    getDocs(collection(db, collectionMessageName)).then((querySnapshot)=>{
      querySnapshot.forEach((doc) => {
        // (getrecipenum)
        if(String(getrecipenum) === String(doc.data().recipeCommentNum)){
          messageAry.push({
            id : doc.id,
            ...doc.data()
          })
        }
      })
    }).then(()=>{
      setMessage([...messageAry])
      setCommentCount(messageAry.length)
    })};

  useEffect(() => {
    fetchUsersData()
    fetchRecipeData()
    fetchMessageData()
  },[getuid]);

// 
  const [goodcount , setGoodCount] = useState("")
  const handleGoodIcon = () => {
    // 
    setGoodAdd(false)
    setGoodCansel(false)
    setBookMarkAdd(false)
    setBookMarkCansel(false)
    var count = 0
    var documentOKFlg = 0
    var buffDocID = ""
    const firestore = firebaseApp.firestore
    // recipe
    getDocs(collection(db, collectionRecipeName)).then((querySnapshot)=>{
      querySnapshot.forEach((document) => {
        // good1
        if(String(document.id)===String(recipe[0].id)){
          const docRef = collection(firestore , collectionRecipeName+"/"+document.id+collectionGoodName)
          // good
          const goodCollectionRef = collectionRecipeName+"/"+document.id+collectionGoodName
          getDocs(collection(db, collectionRecipeName+"/"+document.id+collectionGoodName)).then((querySnapshot)=>{
            querySnapshot.forEach((documentGood) => {
              count = count +1
              documentOKFlg = documentOKFlg + 1
              if(String(documentGood.data().uid)===String(store.getState().loginUserUID)){
                // good
                buffDocID = documentGood.id
                console.log("documentOKFlg : ",documentOKFlg)
                documentOKFlg = documentOKFlg + 1
              }else{
                // good
                console.log("documentOKFlg : ",documentOKFlg)
              }
            })
            console.log("documentOKFlg : ",documentOKFlg)
            if(count === 0 && documentOKFlg === 0){
              // 0
              console.log("0")
              addDoc(docRef, {
                uid : profile.uid,
                clickdate: Timestamp.fromDate(new Date()),  // 
              })
              count = count + 1
              setGoodCount(count)
              setGoodAdd(true)
              setGoodFlg(true)
            }else if(documentOKFlg > 1){
              console.log("good")
              deleteDoc(doc(db , goodCollectionRef , buffDocID))
              count = count - 1
              setGoodCount(count)
              setGoodCansel(true)
              setGoodFlg(false)
            }else if(documentOKFlg === 1){
              console.log("1")
              addDoc(docRef, {
                uid : profile.uid,
                clickdate: Timestamp.fromDate(new Date()),  // 
              })
              count = count + 1
              setGoodCount(count)
              setGoodAdd(true)
              setGoodFlg(true)
            }
          })
        }
      })
    }).then(()=>{
      // 
    })
  }

// 
const [bookmarkcount , setBookMarkCount] = useState("")
  const handleBookMarkIcon = () => {
    // 
    setGoodAdd(false)
    setGoodCansel(false)
    setBookMarkAdd(false)
    setBookMarkCansel(false)
    var count = 0
    var documentOKFlg = 0
    var buffDocID = ""
    const firestore = firebaseApp.firestore
    // recipe
    getDocs(collection(db, collectionRecipeName)).then((querySnapshot)=>{
      querySnapshot.forEach((document) => {
        // bookmark
        if(String(document.id)===String(recipe[0].id)){
          const docRef = collection(firestore , collectionRecipeName+"/"+document.id+collectionBookMarkName)
          // bookmark
          const bookmarkCollectionRef = collectionRecipeName+"/"+document.id+collectionBookMarkName
          getDocs(collection(db, collectionRecipeName+"/"+document.id+collectionBookMarkName)).then((querySnapshot)=>{
            querySnapshot.forEach((documentBookMark) => {
              count = count +1
              documentOKFlg = documentOKFlg + 1
              if(String(documentBookMark.data().uid)===String(store.getState().loginUserUID)){
                // good
                buffDocID = documentBookMark.id
                console.log("documentOKFlg : ",documentOKFlg)
                documentOKFlg = documentOKFlg + 1
              }else{
                // good
                console.log("documentOKFlg : ",documentOKFlg)
              }
            })
            console.log("documentOKFlg : ",documentOKFlg)
            if(count === 0 && documentOKFlg === 0){
              // 0
              console.log("0")
              addDoc(docRef, {
                uid : profile.uid,
                clickdate: Timestamp.fromDate(new Date()),  // 
              })
              count = count + 1
              setBookMarkCount(count)
              setBookMarkAdd(true)
              setBookMarkFlg(true)
            }else if(documentOKFlg > 1){
              console.log("good")
              deleteDoc(doc(db , bookmarkCollectionRef , buffDocID))
              count = count - 1
              setBookMarkCount(count)
              setBookMarkCansel(true)
              setBookMarkFlg(false)
            }else if(documentOKFlg === 1){
              console.log("1")
              addDoc(docRef, {
                uid : profile.uid,
                clickdate: Timestamp.fromDate(new Date()),  // 
              })
              count = count + 1
              setBookMarkCount(count)
              setBookMarkAdd(true)
              setBookMarkFlg(true)
            }
          })
        }
      })
    }).then(()=>{
      // 
    })
  }

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <RecipeDetailsHeader/>
      {recipe ? ( 
          recipe.map((recipe) => (
      <Box>
        <Box
          sx={{
            bgcolor: 'background.paper',
            pt: 1,
            pb: 6,}}>
          <Container maxWidth="md">
            {/*  */}
            <Typography
              sx={{ 
                p: 1, 
                pl : 3,
                fontSize: 32 , 
                background: "#faf0e6", 
                borderRadius: 1 ,
                color:"#a0522d",
                borderLeft : "solid #a0522d 6px",}}>
              <strong>{recipe.title}</strong>
            </Typography>
            <Box
              sx={{
                pl : 4,
                pr : 4,}}>
              <Grid container spacing={4}>
                <Grid item xs={1}>
                  {/*  */}
                  <Avatar src={getavatarurl} alt="" />
                </Grid>
                <Grid item xs={5}>
                  {/*  */}
                  <Typography 
                    sx={{ 
                      p: 1, 
                      fontSize: 14 , 
                      width : 600 , 
                      color:"#000000"}}>
                    {/* uid */}
                    <Link href={`/profiles/${recipe.image.uid}`} color="#000000">
                      {recipe ? recipe.image.user : ""}
                    </Link>
                  </Typography>
                </Grid>
                <Grid item xs={6} align="center">
                  {/*  */}
                  <Typography 
                    sx={{ 
                      p: 1, 
                      fontSize: 14 , 
                      width : 600 , 
                      color:"#000000"}}>
                    {format(recipe.createdAt.toDate(), "yyyyMMdd")}
                  </Typography>
                </Grid>
              </Grid>
            </Box>
          </Container>
        </Box>
        <Container sx={{ py: 1 }} maxWidth="md">
        <Box
          sx={{
            bgcolor: '#eeeeee',
            pb : 4,
            pl : 4,
            pr : 4,}}>
          <Grid container spacing={4}>
            <Grid item xs={6}>
              {/*  */}
              <Typography variant="body2" align='left'>
                <CardMedia
                  component = "img"
                  height    = "400"
                  image     = {recipe.image.url}
                  alt       = "Paella dish"/>
              </Typography>
            </Grid>
            <Grid item xs={6}>
              {/*  */}
              <Grid>
                <Typography 
                  sx={{ 
                    pl : 1 ,
                    fontSize: 14 , 
                    color:"#a0522d",
                    borderLeft : "solid #a0522d 6px"}}>
                  <strong></strong>
                </Typography>
              </Grid>
              <Grid container spacing={4}>
                <Grid item xs={6}>
                <Typography 
                  sx={{ 
                    pl : 2,
                    fontSize: 14 , 
                    color:"#000000"}}>
                  
                </Typography>
                  <Typography 
                    sx={{ 
                      p: 1, 
                      fontSize: 16 , 
                      background: "#ffffff", 
                      borderRadius: 1 ,
                      color:"#000000"}}>
                    {recipe.category}
                  </Typography>
                </Grid>
                <Grid item xs={6}>
                  <Typography 
                    sx={{ 
                      pl : 2 ,
                      fontSize: 14 , 
                      color:"#000000"}}>
                    
                  </Typography>
                  <Typography 
                    sx={{ 
                      p: 1, 
                      fontSize: 16 , 
                      background: "#ffffff", 
                      borderRadius: 1 ,
                      color:"#000000"}}>
                    {recipe.detail}
                  </Typography>
                </Grid>
              </Grid>
              <br/>
              {/*  */}
              <Typography 
                sx={{ 
                  pl : 1 ,
                  fontSize: 14 , 
                  color:"#a0522d",
                  borderLeft : "solid #a0522d 6px"}}>
                <strong></strong>
              </Typography>
              <Typography 
                sx={{ 
                  p: 1, 
                  fontSize: 16 , 
                  background: "#ffffff", 
                  borderRadius: 1 ,
                  color:"#000000"}}>
                {Number(recipe.cost).toLocaleString()} 
              </Typography>
              <br/>
              {/*  */}
              <Typography 
                sx={{ 
                  pl : 1 ,
                  fontSize: 14 , 
                  color:"#a0522d",
                  borderLeft : "solid #a0522d 6px"}}>
                <strong></strong>
              </Typography>
              <Typography 
                sx={{ 
                  p: 1, 
                  fontSize: 16 , 
                  background: "#ffffff", 
                  borderRadius: 1 ,
                  color:"#000000"}}>
                 {recipe.period}
              </Typography>
            </Grid>
          </Grid>
          {/*  */}
          <br/>
          <Grid>
            <Typography 
              sx={{ 
                pl : 1 ,
                fontSize: 14 , 
                color:"#a0522d",
                borderLeft : "solid #a0522d 6px"}}>
              <strong></strong>
            </Typography>
          </Grid>
          <Grid>
            <Typography 
              sx={{ 
                p: 1, 
                fontSize: 16 , 
                background: "#ffffff", 
                borderRadius: 1 ,
                color:"#000000"}}>
              {recipe.memo}
            </Typography>
          </Grid>
        </Box>
        <Box
          sx={{
            bgcolor: '#eeeeee',
            pl : 4,
            pr : 4,}}>
          <Grid container spacing={0} >
            <Grid item xs={2}>
              {/* / */}
              {/* (color:"#4169e1")
                  
                  good
                   */}
              {goodflg ? 
              <Typography 
                sx={{ 
                  fontSize: 14 , 
                  color:"#4169e1"}}>
                <IconButton
                  onClick={handleGoodIcon}>
                  <Tooltip title="" arrow>
                    <ThumbUpAltIcon sx={{ color : "#4169e1" ,fontSize: 20}}/>
                  </Tooltip>
                </IconButton>
                {goodcount}
              </Typography>:
              <Typography 
                sx={{ 
                  fontSize: 14 , 
                  color:"#000000"}}>
                <IconButton
                  onClick={handleGoodIcon}>
                  <Tooltip title="" arrow>
                    <ThumbUpAltIcon sx={{ color : "#696969" ,fontSize: 20}}/>
                  </Tooltip>
                </IconButton>
                {goodcount}
              </Typography> }
            </Grid>
            <Grid item xs={3}>
              {/* / */}
              {/* (color:"#4169e1")
                  
                  bookmark
                   */}
               {bookmarkflg ? 
               <Typography 
                sx={{ 
                  fontSize: 14 , 
                  color:"#ffa500"}}>
                <IconButton
                  onClick={handleBookMarkIcon}>
                  <Tooltip title="" arrow>
                    <StarsIcon sx={{ color : "#ffa500" ,fontSize: 20}}/>
                  </Tooltip>
                </IconButton>
                {bookmarkcount}
              </Typography> : 
              <Typography 
                sx={{ 
                  fontSize: 14 , 
                  color:"#000000"}}>
                <IconButton
                  onClick={handleBookMarkIcon}>
                  <Tooltip title="" arrow>
                    <StarsIcon sx={{ color : "#696969" ,fontSize: 20}}/>
                  </Tooltip>
                </IconButton>
                {bookmarkcount}
              </Typography>}
            </Grid>
          </Grid>
        </Box>
        {goodadd && <Alert severity="success"></Alert>}
        {goodcansel && <Alert severity="error"></Alert>}
        {bookmarkadd && <Alert severity="success"></Alert>}
        {bookmarkcansel && <Alert severity="error"></Alert>}
        <br/>
        {/*  */}

          <Grid container spacing={0}>
            <Typography
              sx={{ 
                p: 1, 
                fontSize: 20 , 
                color:"#000000"}}>
              <strong></strong> 
            </Typography>
            <Typography
              sx={{ 
                p: 1, 
                fontSize: 20 , 
                color:"#E64545"}}>
              <strong>{commentcount}</strong>
            </Typography>
            <Typography
              sx={{ 
                p: 1, 
                fontSize: 20 , 
                color:"#000000"}}>
              <strong></strong>
            </Typography>
          </Grid>
          <Box
          sx={{
            bgcolor: '#ffffff',
            pb : 4,
            pl : 4,
            pr : 4,}}>
          <Grid container spacing={0} >
            {/* & */}
            <Grid item xs={12} align = "center">
              {/*  */}
              <Message/>
            </Grid>
          </Grid>
        </Box>
        <br/>
        {/*  */}
        <Grid container spacing={0} >
          <Grid item xs={12} align = "right">
            {/*  */}
            {profile && profile.uid === recipe.image.uid ? 
            <Button 
              variant = "contained"
              onClick = {handleClick}
              sx = {{width : 200}}>
                
            </Button> : ""}
          </Grid>
        </Grid>
        <br/>
          <Typography
            component="h1"
            variant="h5"
            align="left"
            color="text.primary"
            gutterBottom
            sx = {{backgroundColor : "#ffffff",
                    color : "#000000",
                    padding: "1rem 2rem",
                    borderBottom: "double #E64545 6px",
                    pb : 1 ,}}>
            
          </Typography>
          <RecipeDetailsImageModule
            detail    = {recipe.detail}
            recipeNum = {recipe.recipenum}/>
        </Container>
      </Box>
      ))) : (
        <p></p>)}
        {/*  */}
      <Footer/>
    </ThemeProvider>
  );
}