import React, 
      {useState,}     from "react";
import {Avatar,
        AppBar,
        Toolbar,
        Box,
        ListItemIcon,
        MenuItem,
        Menu ,}       from "@mui/material"
import InfomationButton from './InfomationButton';
import EditIcon       from '@mui/icons-material/Edit';
import HomeIcon       from '@mui/icons-material/Home';
import PersonAddIcon  from '@mui/icons-material/PersonAdd';
import LoginIcon      from '@mui/icons-material/Login';
import PersonIcon     from '@mui/icons-material/Person';
import LogoutIcon     from '@mui/icons-material/Logout';
import useProfile     from "../hooks/useProfile"
import HeaderTitle    from '../HeaderTitle'
import IconButton     from '@mui/material/IconButton'
import StarIcon       from '@mui/icons-material/Star'
import BugReportIcon from '@mui/icons-material/BugReport'

export default function InfomationHeader() {
  const profileData = useProfile()
  const profile = profileData.profile
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);

  // 
  const handleClickAvatar = (event) =>{
    setAnchorEl(event.currentTarget)
  }
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
      <Box sx={{ flexGrow: 1 }} >
        <AppBar position="fixed" >
          <Toolbar>
          <HeaderTitle
              variant   = "h6"
              noWrap
              component = "div"
              sx={{ display: { xs: 'none', sm: 'block' } }}/>
            <Box sx={{ flexGrow: 1 }}/>
            <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
              {profile ? 
                <InfomationButton
                  text      = "" 
                  link      = "/"
                  size      = "large"
                  variant   = "contained"
                  sx        = {{"&:hover": {background: "#E64545"}}}
                  startIcon = {<HomeIcon/>}/>
              : "" }
              {profile ? 
                <InfomationButton
                  text      ="" 
                  link      = "/postpage"
                  size      = "large"
                  variant   = "contained"
                  sx        = {{"&:hover": {background: "#E64545"}}}
                  startIcon = {<EditIcon/>}/>
                : "" }
              {profile ? 
                <InfomationButton
                  text      = "" 
                  link      = {profile ? "/profiles/"+profile.uid : ""} 
                  size      = "large"
                  variant   = "contained"
                  sx        = {{"&:hover": {background: "#E64545"}}}
                  startIcon = {<PersonIcon/>}/>
                  : "" }
              {profile ? 
                <InfomationButton
                  id        = "logout"
                  text      = "" 
                  link      = "/"
                  size      = "large"
                  variant   = "contained"
                  sx        = {{"&:hover": {background: "#E64545"}}}
                  startIcon = {<LogoutIcon/>}/>
                : <InfomationButton
                  id        = "login"
                  text      = "" 
                  link      = "/login"
                  size      = "large"
                  variant   = "contained"
                  sx        = {{"&:hover": {background: "#E64545"}}}
                  startIcon = {<LoginIcon/>}/>}
              {profile ? 
                <IconButton onClick={handleClickAvatar}>
                  <Avatar src={profile ? profile.image : ""} alt="" />
                </IconButton>
                  : <InfomationButton
                    text    = "" 
                    link    = "/signup"
                    size    = "large"
                    variant = "contained"
                    sx        = {{"&:hover": {background: "#E64545"}}}
                    startIcon = {<PersonAddIcon/>}/>}
              </Box>
          </Toolbar>
        </AppBar>
      </Box>
  )
}
