import React, { useState } from "react"
import {Avatar,
        Alert,
        Button,
        CssBaseline,
        TextField,
        Box,
        Typography,
        Container,
        Grid,}             from "@mui/material"
import LockOutlinedIcon    from "@mui/icons-material/LockOutlined"
import usePasswordReset    from "../hooks/passWordReSetting"
import PasswordResetHeader from "./PasswordResetHeader"
import {createTheme, 
        ThemeProvider }    from '@mui/material/styles';
import SendIcon            from '@mui/icons-material/Send';  
import PasswordResetButton from "./PasswordResetButton"
import ArrowBackIcon       from '@mui/icons-material/ArrowBack';

const theme = createTheme({
    shadows: ["none"],
    palette: {
        // 
        primary: {
          main: '#E64545',
          contrastText: '#ffffff',
        },
        // 
        background: {
          default: '#ffffff',
        },
        // 
        text: { primary: '#000000' },
      },
});

export default function PasswordReset() {
    const { success, error, passwordReset } = usePasswordReset()
    const [email, setEmail] = useState("")

    // 
    const handleSubmit = (event) => {
        event.preventDefault()
        passwordReset(email)
    }

  return (
    <ThemeProvider theme={theme}>      
        <Container maxWidth>
            <PasswordResetHeader/>
        </Container>
        <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
            sx={{
            marginTop: 8,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",}}>
            <Avatar sx={{ m: 1, bgcolor: "#E64545" }}>
            <LockOutlinedIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
            
            </Typography>
            <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
                margin="normal"
                required
                fullWidth
                id="email"
                label=""
                name="email"
                autoComplete="email"
                autoFocus
                value={email}
                onChange={e => setEmail(e.target.value)}/>
            <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
                startIcon = {<SendIcon/>}>
                
            </Button>
            <Grid container sx={{ justifyContent: "center" }}>
                <PasswordResetButton
                    fullWidth
                    text = ""
                    variant = "outlined"
                    link    = "login"
                    startIcon = {<ArrowBackIcon/>}/>
            </Grid>
            </Box>
        </Box>
        {error && (<Alert severity="error"></Alert>)}
        {success && (<Alert severity="success"></Alert>)}
        </Container>
    </ThemeProvider>
  )
}