import   React, 
      {  useEffect , 
         useState }      from "react"
import { Alert , 
         Avatar ,
         Typography ,
         Paper ,  
         Box ,
         Grid,
         Container ,
         createTheme , 
         ThemeProvider ,
         CssBaseline ,
         TextField , 
         Input,
         Button, 
         FormControl,
         InputLabel,
         Select ,
         MenuItem ,
         CardMedia,}      from "@mui/material"
import { firebaseApp }    from "../../firebase"
import   useUser          from "../hooks/getuseAuth"
import   PostPageHeader   from './PostPageHeader';
import   PostPageButton   from './PostPageButton';
import { styled }         from '@mui/material/styles';
import   EditIcon         from '@mui/icons-material/Edit';
import   ArrowBackIcon    from '@mui/icons-material/ArrowBack';
import   store            from '../../store/index';
import   useProfile       from "../hooks/useProfile"
import { useHistory}      from "react-router-dom";
import { ref,
         uploadBytes,
         getDownloadURL,} from "firebase/storage"
import { addDoc,
         collection,
         Timestamp,
         getDocs , }      from "firebase/firestore"
import { db }             from '../../firebase';
import Footer            from '../Footer';

const collectionCategoryName = "category"

const defaultSrc =
    "https://firebasestorage.googleapis.com/v0/b/myfirebasesample-c6d99.appspot.com/o/PAGE_USE_IMG%2FAddImage.png?alt=media&token=d4acd7c6-5a2b-4f54-a4bb-0e6240d25f81";
const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const theme = createTheme({
  shadows: ["none"],
  palette: {
    // 
    primary: {
      main: '#E64545',
      contrastText: '#ffffff',
    },
    // 
    background: {
      default: '#ffffff',
    },
    // 
    text: { primary: '#000000' },
  },
});

export default function PostPage() {
  const [name, setName] 
    = useState(store.getState().displayName)      // 
  const [recipetitle, setRecipeTitle] 
    = useState("")      // 
  const [productioncost , setProductionCost] 
    = useState("")   // 
  const [productionperiod , setProductionPeriod] 
    = useState("") // 
  const [productionmemo , setProductionMemo] 
    = useState("")   // 
  const [error, setError] = useState(false)             // 
  const [errormessage , setErrorMessage] = useState("") // 
  const [success, setSuccess] = useState(false)         // 
  const [image, setImage] = useState()
  const firestorage = firebaseApp.firestorage
  const profileData = useProfile()
  const profile = profileData.profile
  const { user } = useUser()
  const history = useHistory()

  // ------------
  const [categorys, setCategorys] = useState([])
  const [detail, setDetail] = useState([])
  const categoryAry = []
  const detailAry = []
  const [selectcategory , setSelectCategory] = useState("")  // 
  const [selectdetail , setSelectDetail]     = useState("")  // 

  // category
  const handleSelectChange = (event) => {
    setSelectCategory(event.target.value)
    setSelectDetail("")
    fetchDetailData(event.target.value)
  }

  // detail
  const handleDetailChange = (event) => {
    setSelectDetail(event.target.value)
    console.log(event.target.value)
  };

  // firestorecategory
  const fetchCategoryData = () => {
    getDocs(collection(db, collectionCategoryName)).then((querySnapshot)=>{
      querySnapshot.forEach((doc) => {
        // 
        if(!categoryAry.includes(doc.data().category)){
          // 
          if(doc.data().category !== ""){
            categoryAry.unshift(
              doc.data().category)
          }else{
            categoryAry.push(
              doc.data().category)
          }
        }
      })
    }).then(()=>{
      setCategorys([...categoryAry])
      console.log("categoryAry : " , categoryAry)
    })}

  // firestoredetail
  const fetchDetailData = (detail) => {
    console.log("detail => ", detail)
    getDocs(collection(db, collectionCategoryName)).then((querySnapshot)=>{
      querySnapshot.forEach((doc) => {
        // 
        if(doc.data().category === detail){
          // 
          if(doc.data().detail !== ""){
            detailAry.unshift(
              doc.data().detail)
          }else{
            detailAry.push(
              doc.data().detail)
          }
        }
      })
    }).then(()=>{
      setDetail([...detailAry])
      console.log("detailAry : " , detailAry)
    })};
      
    useEffect(() => {
      fetchCategoryData()
    },[]);
  // ------------

  const handleChange = (e) => {
    console.log(e.target.files)
    if (e.target.files !== null) {
        setImage(e.target.files[0])
        console.log("handleChange ~> ",e.target.files[0])
        console.log("image.name => " , image)
      }
  }

  // 
  const handleSubmit = (event) => {
    event.preventDefault()
    const firestore = firebaseApp.firestore
    // 
    setError(false)
    setSuccess(false)

    // 
    if(recipetitle ===""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(selectcategory ===""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(selectdetail ===""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(productioncost ===""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(productionperiod ===""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(productionmemo ===""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(!image){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }

    try {
      const docRef = collection(firestore, "recipe");

      if(image){
          const recipeNo = Math.floor(Math.random()* 1000000000000001)
          const imageRef = ref(firestorage, '/RECIPE_IMG/' + recipeNo + "/" + image.name)
          // firebase strage
          uploadBytes(imageRef, image).then(() => {
            getDownloadURL(imageRef).then(url => {
              console.log(url)
              // firestore
              addDoc(docRef,{
                category         : selectcategory,                 // 
                detail           : selectdetail,                 // 
                createdAt        : Timestamp.fromDate(new Date()), // 
                memo             : productionmemo ,                // 
                cost             : productioncost ,                // 
                period           : productionperiod ,              // 
                title            : recipetitle ,                   // 
                recipenum        : recipeNo ,                      // No
                image: {
                  filename       : image.name,                     // 
                  user           : profile.name,                   // DIY
                  url            : url,                            // URL
                  uid            : profile.uid,},                  // UID
                })
              })
            console.log("!")
            // 
            setSuccess(true)
            setTimeout(() => {
              history.push("/")
            },2000)
          })
        }
          } catch (err) {
            console.log("")
            console.log(err)
            // 
            setError(true)
          }
        }
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Container maxWidth>
        <PostPageHeader />
      </Container>
      <Container maxWidth = "md">
        <Container component="main" maxWidth="lg" sx={{ mb: 4 }}>
          <Paper variant="outlined" sx={{ my: { xs: 3, md: 6 }, p: { xs: 2, md: 3 } }}>
            <Typography 
              component="h1" 
              variant="h4" 
              align="center"
              sx = {{backgroundColor : "#ffffff",
                     color : "#000000",
                     padding: "1rem 2rem",
                     borderTop: "double #E64545 6px",
                     borderBottom: "double #E64545 6px",
                     pt : 2 , 
                     pb : 2 ,}}>
              DIY
            </Typography>
            <br/>
            <Typography 
              component="h1" 
              variant="h6" 
              align="left"
              sx = {{backgroundColor : "#ffffff",
                     color : "#000000",
                     padding: "0.2rem",
                     borderLeft : "solid #E64545 6px"}}>
              
            </Typography>
            <Grid container spacing={1} align= "center">
                {/* <Item><ImageUpload/></Item> */}
                <Grid item xs={12} md={12}>
                  <CardMedia
                    sx={{ width: 400, height: 400 }}
                    // src={image ? URL.createObjectURL(image) : profile ? profile.image : ""}alt=""/>
                    image={image ? URL.createObjectURL(image) : defaultSrc}alt=""/>
                  <input
                    id="image"
                    type="file"
                    accept="image/*"
                    onChange={handleChange}
                    style={{ display: "none" }}/>
                    <label htmlFor="image">
                      <Button 
                        variant="outlined" 
                        color="primary" 
                        component="span"
                        sx = {{width : 400}}>
                        
                      </Button>
                    </label>
                </Grid>
                {/* <Item>2<ImageUpload/></Item>
                <Item>3<ImageUpload/></Item> */}
            </Grid>
            <br/>
            <Grid container spacing={2}>
              <Grid item xs={12} md={12}>
                <Typography 
                  variant="h6" 
                  gutterBottom
                  sx = {{backgroundColor : "#ffffff",
                         color : "#000000",
                         padding: "0.2rem",
                         borderLeft : "solid #E64545 6px"}}>
                  
                </Typography>
                <Box
                  sx={{
                    width: 500,
                    maxWidth: '100%',}}>
                  <TextField
                    fullWidth
                    id           = "recipetitle"
                    name         = "recipetitle"
                    label        = ""
                    defaultValue = ""
                    onChange={e => 
                      setRecipeTitle(e.target.value)}/>
                </Box>
              </Grid>
              <Grid item xs={12} md={6}>
                <Typography 
                  variant="h6" 
                  gutterBottom
                  sx = {{backgroundColor : "#ffffff",
                         color : "#000000",
                         padding: "0.2rem",
                         borderLeft : "solid #E64545 6px"}}>
                  
                </Typography>
                <FormControl fullWidth >
                  <InputLabel id="category-label">
                    
                  </InputLabel>
                  {/*  */}
                  <Select
                    labelId  = "category-name-label"
                    id       = "category-name"
                    value    = {selectcategory}
                    onChange = {handleSelectChange}
                    label    = "">
                    {categorys.map((categorys) => (
                      <MenuItem
                        key   = {categorys}
                        value = {categorys}>
                        {categorys}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={12} md={6}>
                <Typography 
                  variant="h6" 
                  gutterBottom
                  sx = {{backgroundColor : "#ffffff",
                         color : "#000000",
                         padding: "0.2rem",
                         borderLeft : "solid #E64545 6px"}}>
                  
                </Typography>
                <FormControl fullWidth >
                  <InputLabel id="detail-label">
                    
                  </InputLabel>
                  {/*  */}
                  <Select
                    labelId  = "detail-name-label"
                    id       = "detail-name"
                    value    = {selectdetail}
                    onChange = {handleDetailChange}
                    label    = "">
                    {detail.map((detail) => (
                      <MenuItem
                        key   = {detail}
                        value = {detail}>
                        {detail}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
              
              <Grid item xs={12} md={6}>
                <Typography 
                  variant="h6" 
                  gutterBottom
                  sx = {{backgroundColor : "#ffffff",
                         color : "#000000",
                         padding: "0.2rem",
                         borderLeft : "solid #E64545 6px"}}>
                  
                </Typography>
                <Typography variant="h7">
                  
                  <TextField
                    id           = "productionCost"
                    name         = "productionCost"
                    size         = "small"
                    type         = "number"
                    label        = ""
                    defaultValue = ""
                    onChange={e => 
                      setProductionCost(e.target.value)}/>
                  
                </Typography>
              </Grid>
                <br/><br/>
              <Grid item xs={12} md={6}>
                <Typography 
                  variant="h6" 
                  gutterBottom
                  sx = {{backgroundColor : "#ffffff",
                         color : "#000000",
                         padding: "0.2rem",
                         borderLeft : "solid #E64545 6px"}}>
                  
                </Typography>
                <Typography variant="h7">
                    
                  <TextField
                    id           = "productionPeriod"
                    name         = "productionPeriod"
                    size         = "small"
                    label        = ""
                    defaultValue = ""
                    onChange={e => 
                      setProductionPeriod(e.target.value)}/>
                </Typography>
              </Grid>

              <Grid item xs={12} md={12}>
                <Typography 
                  variant="h6" 
                  gutterBottom
                  sx = {{backgroundColor : "#ffffff",
                         color : "#000000",
                         padding: "0.2rem",
                         borderLeft : "solid #E64545 6px"}}>
                  
                </Typography>
                <TextField
                  fullWidth
                    id       = "memo"
                    label    = ""
                    name     = "memo"
                    multiline
                    rows={6}
                    defaultValue = ""
                    onChange={e => 
                      setProductionMemo(e.target.value)}/>
              </Grid>
            </Grid>
            {/*  */}
            {/* <Stack
                direction={{ xs: 'column', sm: 'row' }}
                spacing={{ xs: 1, sm: 2, md: 10 }}>
              //  <Item><ImageUpload/></Item> 
                <Grid item xs={12} md={12}>
                  <Avatar
                    sx={{ width: 100, height: 100 }}
                    // src={image ? URL.createObjectURL(image) : profile ? profile.image : ""}alt=""/>
                    src={image ? URL.createObjectURL(image) : defaultSrc}alt=""/>
                  <input
                    id="image"
                    type="file"
                    accept="image/*"
                    onChange={handleChange}
                    style={{ display: "none" }}/>
                    <label htmlFor="image">
                      <Button 
                        variant="contained" 
                        color="primary" 
                        component="span">
                        
                      </Button>
                    </label>
                </Grid>
                // <Item>2<ImageUpload/></Item>
                // <Item>3<ImageUpload/></Item> 
                </Stack> */}

            <Box sx={{ flexGrow: 1 }}>
              <Grid container spacing={1}>
                <Grid item xs={6}>
                <Container align="center">
                  <PostPageButton
                    id   = "postreturn"
                    variant = "outlined"
                    sx={{ mt: 1, mb: 2 }}
                    fullWidth
                    startIcon = {<ArrowBackIcon/>}
                    link ="/"
                    text = ""/>
                  </Container>
                </Grid>
                <Grid item xs={6}>
                  <Container align="center">
                    <Button
                      id   = "postok"
                      variant="contained"
                      sx={{ mt: 1, mb: 2 }}
                      fullWidth
                      startIcon = {<EditIcon/>}
                      onClick = {handleSubmit}>
                        
                    </Button>
                  </Container>
                </Grid>
              </Grid>
            </Box>
            {/*  */}
            {success && <Alert severity="success"></Alert>}
            {/*  */}
            {error && <Alert severity="error">{errormessage}</Alert>}
          </Paper>
        </Container>
      </Container>
      <Footer/>
    </ThemeProvider>
  );
}