import  React ,
      { useEffect,
        useState,
        useRef , }           from "react";
import ThumbUpAltIcon from '@mui/icons-material/ThumbUpAlt';
import StarsIcon from '@mui/icons-material/Stars';
import InsertCommentIcon from '@mui/icons-material/InsertComment';
import { collection,
         getDocs ,}          from 'firebase/firestore';
import { Typography , 
         Box ,
         Grid,
         createTheme , 
         ThemeProvider ,
         Link,
         Button,}            from "@mui/material"
import { format ,
         formatDistance,  }  from "date-fns"
import { db }                from '../../firebase';
import { useHistory}         from 'react-router';
import { ja }                from "date-fns/locale"
import   useProfile          from "../hooks/useProfile"
import   Paper               from '@mui/material/Paper';
import { styled }            from '@mui/material/styles';
import   ProfilesImageButton from "./ProfilesImageButton";
import { firebaseApp }       from "../../firebase";


const collectionRecipeName = "recipe"
const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#ffffff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));
const theme = createTheme()

export default function ProfileImageList() {
  const [recipe, setRecipe] = useState([]);
  const [goodcount , setGoodCount] = useState(0)
  const [bookmarkcount , setBookMarkCount] = useState(0)
  const profileData = useProfile()             // 
  var profile = profileData.profile            // 
  const bottomRef = useRef(null)
  const recipeAry = [];
  const history = useHistory()
  const firestorage = firebaseApp.firestorage  // 

  // 
  const handleSubmit = (event) => {
    history.push("/recipedetails/edit")
  }

  // pathnameuid
  const uidAry = window.location.pathname.split("/")
  const getuid = uidAry[2]

  // 
  const time = (date) => {
    let timestamp = formatDistance(new Date(), date.toDate(), {
      locale: ja,
    })
    if (timestamp.indexOf("") !== -1) {
      return (timestamp = "")
    } else if (
      timestamp.indexOf("") !== -1 ||
      timestamp.indexOf("") !== -1
    ) {
      return (timestamp = format(date.toDate(), "yyyyMd", {
        locale: ja,
      }))
    } else {
      return (timestamp = timestamp + "")
    }
  }

  const fetchRecipeData = () => {
    getDocs(collection(db, collectionRecipeName)).then((querySnapshot)=>{
      querySnapshot.forEach((doc) => {
        // 
        firebaseApp.fireauth.onAuthStateChanged(user => {
            // uidfirestore/recipe/image/uid
            // if(user.uid === doc.data().image.uid){
            if(getuid === doc.data().image.uid){
              recipeAry.push({
                id : doc.id,
                ...doc.data()
              })
            }
          })
        })
    }).then(()=>{
      recipeAry.sort(function(a, b) {
        return (a.createdAt < b.createdAt) ? -1 : 1;  //
      })
      setRecipe([...recipeAry])
    })};

  useEffect(() => {
    fetchRecipeData()
  },[]);

  return (
    <ThemeProvider theme={theme}>
      <Grid container spacing={0} >
        {recipe ? ( 
          recipe.map((recipe) => (
          <Box 
            key={recipe.id} 
            sx={{
              display: "flex",
              my: 2,
              gap: 2,
              flexGrow: 1, m: 2,}}>
            <Grid item xs={2} align = "left">
              <Box>
                {/*  */}
                <ProfilesImageButton
                  imgURL = {recipe.image.url}
                  info   = {recipe}
                  text   = ""
                  link   = "/recipedetail/"/>
              </Box>
            </Grid>
            <Grid item xs={10} align = "left">
            {/*  */}
              <Typography sx={{ fontSize: 18 , width : 380}}>
                {/*  */}
                <Link href={`/recipedetails/${recipe.recipenum}`} color="#000000">
                  <strong>{recipe.title}</strong>
                </Link>
              </Typography>
              <Grid container spacing={0} >
                <Grid item xs={6} align = "center">
                  {/*  */}
                  <Grid container spacing={0} >
                    <Grid item xs={4} align = "left">
                      <ThumbUpAltIcon sx={{ color : "#ffa500" , fontSize: 20 }}/>
                      <Typography color="#000000" variant="caption" fontSize={15}>
                          {recipe.good}
                      </Typography>
                    </Grid>
                    <Grid item xs={4} align = "left">
                      {/*  */}
                      <StarsIcon color="#ffffff" sx={{ color : "#a0522d" , fontSize: 20 }}/>
                      <Typography color="#000000" variant="caption" fontSize={15}>
                        {recipe.bookmark}
                      </Typography>
                    </Grid>
                    {/*  */}
                    {/* <Grid item xs={4} align = "left">
                      <InsertCommentIcon sx={{ color : "#1e90ff" , fontSize: 20 }}/>
                      <Typography color="#000000" variant="caption" fontSize={15}>
                          {goodcount}
                      </Typography>
                    </Grid> */}
                  </Grid>
                </Grid>
                  {/*  */}
                <Grid item xs={6} align = "left">
                  <Typography color="#000000" variant="body2">
                    {format(recipe.createdAt.toDate(), "yyyyMMdd")}
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
          </Box>
          ))) : (
          <p></p>)}
          <div ref={bottomRef}></div>
      </Grid>
    </ThemeProvider>
  );
}
