import   React, 
       { useEffect ,
         useState }              from "react"
import { Alert ,
         Avatar,
         Button,
         Box,
         Grid,
         Typography,
         Container,
         CssBaseline,
         TextField,
         FormControl , 
         InputLabel, 
         Select,
         MenuItem,}              from "@mui/material"
import { createTheme, 
         ThemeProvider }         from '@mui/material/styles';
import { addDoc,
         collection,
         doc,
         getDocs ,
         updateDoc,
         deleteDoc, }            from "firebase/firestore"
import { ref,
         uploadBytes,
         getDownloadURL,}        from "firebase/storage"
import   RecipeDetailsEditHeader from "./RecipeDetailsEditHeader"
import   RecipeDetailsEditButton from "./RecipeDetailsEditButton";
import   Footer                  from '../Footer';
import   CardMedia               from '@mui/material/CardMedia';
import   useProfile              from "../hooks/useProfile"
import { firebaseApp }           from "../../firebase";
import { useHistory }            from 'react-router';
import   store                   from '../../store/index';
import { db }                    from '../../firebase';
import { format }                from "date-fns"

const collectionRecipeName   = "recipe"
const collectionUserName     = "users"
const collectionCategoryName = "category"
const theme = createTheme({
  shadows: ["none"],
  palette: {
    // 
    primary: {
      main: '#E64545',
      contrastText: '#ffffff',
    },
    // 
    background: {
      default: '#ffffff',
    },
    // 
    text: { primary: '#000000' },
  },
});

export default function RecipeDetailsEdit() {
  const [title , setTilte]                   = useState(store.getState().recipetitle)      // 
  const [memo , setMemo]                     = useState(store.getState().productionMemo)   // 
  const [cost , setCost]                     = useState(store.getState().productionCost)   // 
  const [period , setPeriod]                 = useState(store.getState().productionPeriod) // 
  const [createdat , setCreatedAt]           = useState(store.getState().createdAt)        //     
  const [image , setImage]                   = useState()                                  // 
  const [imagename , setImageName]           = useState()                                  // 
  const [error, setError]                    = useState(false)                             // 
  const [success, setSuccess]                = useState(false)                             // 
  const [errormessage , setErrorMessage]     = useState("")                                // 
  const [getavatarurl , setGetAvatarURL]     = useState("")
  const profileData = useProfile()
  const profile = profileData.profile
  const firestorage = firebaseApp.firestorage
  const firestore = firebaseApp.firestore
  const history = useHistory()

  // pathnameNo
  var recipenumAry = window.location.pathname.split("/")
  const getrecipenum = recipenumAry[2]

  // ------START------
  const [category , setCategory]             = useState([])         // (category)
  const [detail , setDetail]                 = useState([])         // (detail)
  const categoryAry = [];
  const detailAry = [];
  const [selectcategory , setSelectCategory] = useState(store.getState().category)
  const [selectdetail , setSelectDetail]     = useState(store.getState().detail)

  // category
  const handleSelectChange = (event) => {
    setSelectCategory(event.target.value)
    setSelectDetail("")
    console.log(event.target.value)
    fetchDetailData(event.target.value)
  }

  // detail
  const handleDetailChange = (event) => {
    setSelectDetail(event.target.value)
    console.log(event.target.value)
  }

  // firestorecategory
  const fetchCategoryData = () => {
    getDocs(collection(db, collectionCategoryName)).then((querySnapshot)=>{
      // recipenumNo
      querySnapshot.forEach((doc) => {
      // 
      if(!categoryAry.includes(doc.data().category)){
        // 
        if(doc.data().category !== ""){
          categoryAry.unshift(
            doc.data().category)
        }else{
          categoryAry.push(
            doc.data().category)
        }
      }
    })
  }).then(()=>{
    setCategory([...categoryAry])
  })}

  // firestoredetail
  const fetchDetailData = (detail) => {
    console.log("detail => ", detail)
    getDocs(collection(db, collectionCategoryName)).then((querySnapshot)=>{
      querySnapshot.forEach((doc) => {
        // 
        if(doc.data().category === detail){
          // 
          if(doc.data().detail !== ""){
            detailAry.unshift(
              doc.data().detail)
          }else{
            detailAry.push(
              doc.data().detail)
          }
        }
      })
    }).then(()=>{
      setDetail([...detailAry])
    })};

  useEffect(() => {
    fetchCategoryData()
    fetchDetailData(store.getState().category)
  },[]);
  // ------END------
  
  // ------START------
  const handleChange = (e) => {
    if (e.target.files !== null) {
      setImage(e.target.files[0])
      setImageName(e.target.files[0].name)
    }
  }
  // ------END------

  // 
  const handleDelete = (id) => {
    console.log("id => " , id)
    if (window.confirm("")) {
            // id
      deleteDoc(doc(db , collectionRecipeName , id)).then((doc) => {
        // fetchUsersData()
        alert("")
        setTimeout(() => {
          history.push("/")
        },1000)
      })
      .catch(() => {
        alert("")
     })
    }else{
    }
  }

  // 
  const handleSubmit = (event) => {
    event.preventDefault()
    // 
    setError(false)
    setSuccess(false)
    // 
    if(title === ""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(memo === ""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(selectcategory === ""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(selectdetail === ""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(cost === ""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(period === ""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    try {
      // const uid = user.uid
      const docRef = collection(firestore, collectionRecipeName);
      if(image){
          const imageRef = ref(firestorage, '/RECIPE_IMG/' + getrecipenum + "/" + image.name)
          // firebase strage
          uploadBytes(imageRef, image).then(() => {
              // getDownloadURLprofileupdateDoc
              // profileaddDoc
              // image
              getDownloadURL(imageRef).then(url => {
                if (profile) {
                  const redipeRef = doc(firestore, collectionRecipeName , store.getState().documentID)
                  updateDoc(redipeRef, {
                    title ,                     // 
                    category : selectcategory , // category()
                    detail : selectdetail ,     // detail()
                    memo ,                      // 
                    cost ,                      // 
                    period ,                    // 
                    image : {
                      filename : image.name ,   // image - 
                      url      : url ,
                      uid      : profile.uid,
                      user     : profile.name}, // image - URL
                  })
                }else{
                  // firestoreURLuid
                  addDoc(docRef, {
                      title,
                      category : selectcategory,
                      detail   : selectdetail,
                      memo,
                      cost,
                      period,
                      image: {
                        filename : image.name,
                        url : url,},
                    })
                  }
              })
            })
          }else{
            // 
            if (profile) {
              const redipeRef = doc(firestore, collectionRecipeName, store.getState().documentID)
              updateDoc(redipeRef, 
                { title , 
                  category : selectcategory, 
                  detail   : selectdetail,
                  memo , 
                  cost , 
                  period})
            } else {
              addDoc(docRef, { 
                title, 
                category : selectcategory, 
                detail   : selectdetail,
                memo,
                cost , 
                period ,
                image : {
                  filename : "" , 
                  url : "" , },
              })
            }}
            console.log("!")
            // 
            setSuccess(true)
            setTimeout(() => {
              history.push("/")
            },2000)
        } catch (err) {
            console.log(err)
            // 
            setError(true)
            setErrorMessage("")
        }
      }

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <RecipeDetailsEditHeader/>
      <Box>
        <Box
          sx={{
            bgcolor: 'background.paper',
            pt: 8,
            pb: 6,}}>
          <Container maxWidth="md">
            {/*  */}
            <Typography 
              sx={{ 
                p: 1, 
                pt : 2 ,
                pb : 2 ,
                fontSize: 20 , 
                width : 600 , 
                color:"#a0522d",
                borderLeft : "solid #a0522d 6px"}}>
              <strong></strong>
            </Typography>
            <TextField 
              fullWidth
              id           = "name"
              name         = "name"
              variant      = "outlined"
              label        = ""
              defaultValue = {title}
              value        = {title ?  title : ""}
              onChange     = {e => setTilte(e.target.value)}
              sx={{ 
                fontSize: 32 , 
                background: "#faf0e6", 
                borderRadius: 1 ,
                color:"#a0522d"}}/>
            <Box
              sx={{
                pt : 1,
                pl : 4,
                pr : 4,}}>
              <Grid container spacing={4}>
                <Grid item xs={1}>
                  {/*  */}
                  <Avatar src={store.getState().photoURL} alt="" />
                </Grid>
                <Grid item xs={5}>
                  {/*  */}
                  <Typography 
                    sx={{ 
                      p: 1, 
                      fontSize: 14 , 
                      width : 600 , 
                      color:"#000000"}}>
                    {store.getState().displayName}
                  </Typography>
                </Grid>
                <Grid item xs={6} align="center">
                  {/*  */}
                  <Typography 
                    sx={{ 
                      p: 1, 
                      fontSize: 14 , 
                      width : 600 , 
                      color:"#000000"}}>
                    {format(createdat.toDate(), "yyyyMMdd")}
                  </Typography>
                </Grid>
              </Grid>
            </Box>
          </Container>
        </Box>
        <Container sx={{ py: 1 }} maxWidth="md">
        <Box
          sx={{
            bgcolor: '#eeeeee',
            pb : 4,
            pl : 4,
            pr : 4,}}>
          <Grid container spacing={4}>
            <Grid item xs={4}>
              <Typography variant="body2" align='left'>
                {/*  */}
                <CardMedia
                  component = "img"
                  height    = "250"
                  image     = {image ? URL.createObjectURL(image) : profile ? store.getState().recipeimage : ""}
                  alt       = ""/>
              </Typography>
              {/*  */}
              <Box component="form"  noValidate sx={{ mt: 4 }}>
                <Container align = "center">
                  <input
                    id="image"
                    type="file"
                    accept="image/*"
                    onChange={handleChange}
                    style={{ display: "none" }}/>
                  <label htmlFor="image">
                    <Button 
                      variant="contained" 
                      color="primary" 
                      component="span"
                      sx = {{width : 200}}>
                      
                    </Button>
                  </label>
                </Container>
              </Box>
              {/*  */}
            </Grid>
            <Grid item xs={8}>
              {/*  */}
              <Grid>
                <Typography 
                  sx={{ 
                    pt       :  1,
                    pb       :  1,
                    pl       :  1,
                    fontSize : 14 , 
                    color    : "#a0522d",
                    borderLeft : "solid #a0522d 6px"}}>
                  <strong></strong>
                </Typography>
              </Grid>
              <Grid container spacing={4}>
                <Grid item xs={12} md={6}>
                  {/* category */}
                  <FormControl fullWidth >
                    <InputLabel id="category-label">
                      
                    </InputLabel>
                    {/* category */}
                    <Select
                      labelId      = "category-name-label"
                      id           = "category-name"
                      defaultValue = {selectcategory}
                      value        = {selectcategory ? selectcategory : store.getState().category}
                      onChange     = {handleSelectChange}
                      label        = ""
                      sx={{ 
                        background: "#ffffff", 
                        borderRadius: 1 ,
                        }}>
                      {category.map((category) => (
                        <MenuItem
                          key   = {category}
                          value = {category ? category : ""}>
                          {category}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                  {/* category  */}
                </Grid>
                <Grid item xs={12} md={6}>
                  {/* detail */}
                  <FormControl fullWidth >
                    <InputLabel id="detail-label">
                      
                    </InputLabel>
                    {/* detail */}
                    <Select
                      labelId      = "detail-name-label"
                      id           = "detail-name"
                      defaultValue = {selectdetail}
                      value        = {selectdetail ? selectdetail : store.getState().detail}
                      onChange     = {handleDetailChange}
                      label        = ""
                      sx={{ 
                        background: "#ffffff", 
                        borderRadius: 1 ,
                        
                        }}>
                      {detail.map((detail) => (
                        <MenuItem
                          key   = {detail}
                          value = {detail ? detail : ""}>
                          {detail}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                  {/* detail  */}
                </Grid>
              </Grid>
              <br/>
              {/*  */}
              <Grid>
                <Typography 
                  sx={{ 
                    pt       :  1,
                    pb       :  1,
                    pl       :  1,
                    fontSize : 14 , 
                    color    : "#a0522d",
                    borderLeft : "solid #a0522d 6px"}}>
                  
                </Typography>
              </Grid>
              <Grid>
                <TextField 
                  fullWidth
                  id           = "name"
                  name         = "name"
                  variant      = "outlined"
                  label        = ""
                  defaultValue = {cost}
                  value        = {cost ? cost : ""}
                  onChange     = {e => setCost(e.target.value)}
                  sx={{ 
                    fontSize: 32 , 
                    background: "#ffffff", 
                    borderRadius: 1 ,
                    color:"#a0522d"}}/>
              </Grid>
              <br/>
              {/*  */}
              <Grid>
                <Typography 
                  sx={{ 
                    pt       :  1,
                    pb       :  1,
                    pl       :  1,
                    fontSize : 14 , 
                    color    : "#a0522d",
                    borderLeft : "solid #a0522d 6px"}}>
                  
                </Typography>
              </Grid>
              <Grid>
                <TextField 
                  fullWidth
                  id           = "name"
                  name         = "name"
                  variant      = "outlined"
                  label        = ""
                  defaultValue = {period}
                  value        = {period ? period : ""}
                  onChange     = {e => setPeriod(e.target.value)}
                  sx={{ 
                    fontSize: 32 , 
                    background: "#ffffff", 
                    borderRadius: 1 ,
                    color:"#a0522d"}}/>
              </Grid>
            </Grid>
          </Grid>
          <br/>
          {/*  */}
          <Grid>
            <Typography 
              sx={{ 
                pt       :  1,
                pb       :  1,
                pl       :  1,
                fontSize : 14 , 
                color    : "#a0522d",
                borderLeft : "solid #a0522d 6px"}}>
              <strong></strong>
            </Typography>
          </Grid>
          <Grid>
            <TextField 
              fullWidth
              multiline
              id           = "name"
              name         = "name"
              variant      = "outlined"
              label        = ""
              defaultValue = {memo}
              value        = {memo ? memo : ""}
              onChange     = {e => setMemo(e.target.value)}
              sx={{ 
                fontSize: 32 , 
                background: "#ffffff", 
                borderRadius: 1 ,
                color:"#a0522d"}}/>
          </Grid>
        </Box>
        <br/>
        {error && <Alert severity="error">{errormessage}</Alert>}
        {success && (<Alert severity="success"> </Alert>)}
          <Grid container spacing={0} >
            <Grid item xs={4} align = "center">
              {/*  */}
              <RecipeDetailsEditButton 
                variant = "outlined"
                text    = ""
                link    = {`/recipedetails/${getrecipenum}`}
                sx = {{width : 200}}/>
            </Grid>
            <Grid item xs={4} align = "center">
              {/*  */}
              <Button
                variant = "outlined"
                sx = {{width : 200}}
                onClick = {() => handleDelete(store.getState().documentID)}>
                  
              </Button>
            </Grid>
            <Grid item xs={4} align = "center">
              {/*  */}
              <Button 
                variant = "contained"
                sx = {{width : 200}}
                onClick = {handleSubmit}>
                  
              </Button>
            </Grid>
          </Grid>
        <br/>
        </Container>
      </Box>
      {/* Footer */}
      <Footer/>
      {/* End footer */}
    </ThemeProvider>
  );
}