import React, { useState } from "react"
import {Avatar,
        Alert,
        Paper,
        Typography,
        CssBaseline,
        Box,
        TextField,
        Button,
        Container,}         from "@mui/material"
import {firebaseApp }       from "../../firebase"
import {ref,
        uploadBytes,
        getDownloadURL,}    from "firebase/storage"
import useUser              from "../hooks/getuseAuth"
import {addDoc,
        collection,
        doc,
        updateDoc, 
        getDocs,}        from "firebase/firestore"
import ProfileHeader        from "./ProfileEditHeader"
import {createTheme, 
        ThemeProvider }     from '@mui/material/styles';
import useProfile           from "../hooks/useProfile"
import ProfileEditSelectBox from "./ProfileEditSelectBox"
import store                from '../../store/index';
import {useHistory}         from "react-router-dom";
import ProfileEditButton    from "./ProfileEditButton"
import Footer               from '../Footer';

const theme = createTheme({
  shadows: ["none"],
  palette: {
    // 
    primary: {
      main: '#E64545',
      contrastText: '#ffffff',
    },
    // 
    background: {
      default: '#ffffff',
    },
    // 
    text: { primary: '#000000' },
  },
});

const collectionUserName = "users"
const collectionRecipeName = "recipe"
const collectionMessageName = "message"

const ProfileEdit = () => {
  const [name, setName] = useState(store.getState().displayName)                   // 
  const [location , setLocation] = useState(store.getState().location)             // 
  const [selfintroduction , setSelfIntroduction] = useState(store.getState().memo) // 
  const [error, setError] = useState(false)                                        // 
  const [success, setSuccess] = useState(false)                                    // 
  const [errormessage , setErrorMessage] = useState("")                            // 
  const [image, setImage] = useState()
  const firestorage = firebaseApp.firestorage
  const firestore = firebaseApp.firestore
  const profileData = useProfile()
  const profile = profileData.profile
  const { user } = useUser()
  const history = useHistory()

  const handleChange = (e) => {
    console.log(e.target.files)
    console.log("handleChange ")
    if (e.target.files !== null) {
        setImage(e.target.files[0])
      }
  }

  const handleSubmit = (event) => {
    event.preventDefault()
    // 
    setError(false)
    setSuccess(false)
    // 
    if(name === ""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(location === ""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }
    if(selfintroduction === ""){
      console.log("")
      setErrorMessage("")
      setError(true)
      return
    }

    try {
        const uid = user.uid
        const docRef = collection(firestore, collectionUserName);
 
        if(image){
            const imageRef = ref(firestorage, 'USER_PROFILE_IMG/' + uid + "/" + image.name)
            // firebase strage
            uploadBytes(imageRef, image).then(() => {
                // getDownloadURLprofileupdateDoc
                // profileaddDoc
                // image
                getDownloadURL(imageRef).then(url => {
                  console.log(url)
                  if (profile) {
                    const userRef = doc(firestore, collectionUserName , profile?.id)
                    updateDoc(userRef, {
                      name,
                      image: url,
                      location,
                      selfintroduction,})
                    // message
                    updateMessage(name , url)
                    // recipe
                    updateRecipe(name)
                  }else{
                    // firestoreURLuid
                    addDoc(docRef, {
                        name,
                        image: url,
                        uid,
                        selfintroduction,
                        location,
                      })
                    }
                })
              })
              }else{
                // 
                if (profile) {
                  const userRef = doc(firestore, collectionUserName, profile?.id)
                  updateDoc(userRef, { name , location , selfintroduction})
                  console.log("bbb")
                  // message
                  updateMessage(name , profile.image)
                  // recipe
                  updateRecipe(name)
              } else {
                addDoc(docRef, { 
                  name, 
                  image: "", 
                  uid ,
                  selfintroduction , 
                  location})
              }}
              console.log("!")
              // 
              setSuccess(true)
              setTimeout(() => {
                history.push("/")
              },2000)
            } catch (err) {
              console.log(err)
              // 
              setError(true)
            }
          }
  
  //  messege
  const updateMessage = (getName , getImage) => {
    getDocs(collection(firestore, collectionMessageName)).then((querySnapshot)=>{
      querySnapshot.forEach((docMessage) => {
        // (getrecipenum)
        if(String(profile.uid) === String(docMessage.data().user.uid)){
          const messageRef = doc(firestore , collectionMessageName , docMessage.id)
          updateDoc(messageRef, { user : { 
                                    name  : getName,
                                    image : getImage,
                                    uid   : docMessage.data().user.uid,}, })
        }
      })
    })
  }

  // recipe
  const updateRecipe = (getName) => {
    console.log("updateRecipe")
    getDocs(collection(firestore, collectionRecipeName)).then((querySnapshot)=>{
      querySnapshot.forEach((docRecipe) => {
        // (getrecipenum)
        if(String(profile.uid) === String(docRecipe.data().image.uid)){
          const recipeRef = doc(firestore , collectionRecipeName , docRecipe.id)
          updateDoc(recipeRef, { image : { 
                                  filename : docRecipe.data().image.filename,
                                  url      : docRecipe.data().image.url,
                                  user     : getName,
                                  uid      : docRecipe.data().image.uid,}, })
        }
      })
    })
  }

  return (
    <ThemeProvider theme={theme}>
        <Container maxWidth>
            <ProfileHeader/>
        </Container>
        <Container maxWidth="sm">
        <CssBaseline />
            <Paper sx={{ m: 4, p: 4 }}>
                {error && <Alert severity="error">{errormessage}</Alert>}
                {success && (<Alert severity="success"> {profile ? "" : ""}</Alert>)}
                <Typography 
                  align="center" 
                  variant="h5"
                  sx = {{color : "#000000",
                         padding: "1rem 2rem",
                         borderTop: "double #E64545 6px",
                         borderBottom: "double #E64545 6px",
                         pt : 2 , 
                         pb : 2 ,}}>
                    
                </Typography>
                <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 4 }}>
                  {/*  */}
                <Container align = "center">
                    <Avatar
                      sx={{ width: 100, height: 100 }}
                      src={image ? URL.createObjectURL(image) : profile ? profile.image : ""}alt=""/>
                  <input
                      id="image"
                      type="file"
                      accept="image/*"
                      onChange={handleChange}
                      style={{ display: "none" }}/>
                  <label htmlFor="image">
                  <Button variant="contained" color="primary" component="span">
                      
                  </Button>
                  </label>
                </Container>
                    <Container align="left">
                      {/*  */}
                      <Typography
                        sx = {{backgroundColor : "#ffffff",
                              color : "#000000",
                              padding: "0.2rem",
                              borderLeft : "solid #E64545 6px"}}>
                        <strong></strong>
                      </Typography>
                      <TextField
                        margin="normal"
                        required
                        fullWidth
                        id="name"
                        name="name"
                        autoComplete="name"
                        autoFocus
                        defaultValue={name}
                        value={name ? name :  ""}
                        onChange={e => 
                          setName(e.target.value)}/>
                    </Container>
                    <br/>
                    <Container align="left">
                      {/*  */}
                      <Typography
                        sx = {{backgroundColor : "#ffffff",
                              color : "#000000",
                              padding: "0.2rem",
                              borderLeft : "solid #E64545 6px"}}>
                        <strong></strong>
                      </Typography>
                      <ProfileEditSelectBox
                        margin       = "normal"
                        fullWidth
                        id           = "location"
                        name         = "location"
                        autoComplete = "location"
                        autoFocus
                        defaultValue={location}
                        value={location ? location : profile ? profile.location : ""}
                        onChange={e => setLocation(e.target.value)}/>
                      </Container>
                      <br/>
                      <Container align="left">
                        {/*  */}
                        <Typography
                          sx = {{backgroundColor : "#ffffff",
                                color : "#000000",
                                padding: "0.2rem",
                                borderLeft : "solid #E64545 6px"}}>
                          <strong></strong>
                        </Typography>
                        <TextField
                          margin="normal"
                          id        ="selfintroduction"
                          fullWidth
                          name = "selfintroduction"
                          autoComplete="selfintroduction"
                          multiline
                          defaultValue={selfintroduction}
                          rows      = {6}
                          value     = {selfintroduction ? selfintroduction : ""}
                          onChange  = {e => setSelfIntroduction(e.target.value)}/>
                      </Container>
                    <Button type="submit" fullWidth variant="contained" sx={{ mt: 3, mb: 2 }}>
                      {profile ? "" : ""}
                    </Button>
                    <ProfileEditButton 
                      id      = "profileeditback"
                      type    = "submit"
                      link    = {profile ? "/profiles/"+profile.uid : ""} 
                      fullWidth 
                      variant = "outlined" 
                      sx={{ mt: 3, mb: 2 }}
                      text = ""/>
                </Box>
            </Paper>
        </Container>
      <Footer/>
    </ThemeProvider>
  )
}

export default ProfileEdit