import React, 
      { useEffect,
        useState,
        useRef ,}           from "react"
import { db }               from '../../firebase'
import {firebaseApp }       from "../../firebase"
import { collection,
         deleteDoc,
         doc ,
         getDocs ,
         addDoc ,
         Timestamp ,}       from 'firebase/firestore'
import { Alert ,
         Avatar ,
         Typography , 
         Box ,
         Grid,
         Stack ,
         TextField,
         Button,
         createTheme , 
         ThemeProvider ,
         Tooltip ,
         CssBaseline ,
         IconButton , }     from "@mui/material"
import { format ,
         formatDistance,  } from "date-fns"
import { ja }               from "date-fns/locale"
import useProfile           from "../hooks/useProfile"
import DeleteIcon           from '@mui/icons-material/Delete'
import ChatBubbleIcon       from '@mui/icons-material/ChatBubble';
import store                from '../../store/index';
import { useHistory }       from "react-router-dom"

const collectionMessageName = "message"

const theme = createTheme({
  shadows: ["none"],
  palette: {
    // 
    primary: {
      main: '#E64545',
      contrastText: '#ffffff',
    },
    // 
    text: { primary: '#000000' },
  },
})

function App() {
  // 
  const [message, setMessage] = useState([])
  const [sendmessage , setSendMessage] = useState("")
  const [error, setError] = useState(false)
  const profileData = useProfile()
  const profile = profileData.profile
  const messageAry = [];
  const bottomRef = useRef(null)
  const history = useHistory()

  // pathnameNo
  var recipenumAry = window.location.pathname.split("/")
  const getrecipenum = recipenumAry[2]
  
  const time = (date) => {
    let timestamp = formatDistance(new Date(), date.toDate(), {
      locale: ja,
    })
    if (timestamp.indexOf("") !== -1) {
      return (timestamp = "")
    } else if (
      timestamp.indexOf("") !== -1 ||
      timestamp.indexOf("") !== -1
    ) {
      return (timestamp = format(date.toDate(), "yyyyMMdd", {
        locale: ja,
      }))
    } else {
      return (timestamp = timestamp + "")
    }
  }
  
  // firestore
  const fetchMessageData = () => {
    getDocs(collection(db, collectionMessageName)).then((querySnapshot)=>{
      querySnapshot.forEach((doc) => {
        // (getrecipenum)
        if(String(getrecipenum) === String(doc.data().recipeCommentNum)){
          messageAry.push({
            id : doc.id,
            ...doc.data()
          })
        }
      })
    }).then(()=>{
      messageAry.sort(function(a, b) {
        return (a.createdAt < b.createdAt) ? -1 : 1;  //
      })
      setMessage([...messageAry])
    })}

  useEffect(() => {
    fetchMessageData()
  },[])

  // 
  const handleDelete = (id) => {
    console.log("id => ",id)
    if (window.confirm("")) {
      console.log("OK " , id)
      
      // id
      console.log("id")
      console.log("ID",id)
      deleteDoc(doc(db , collectionMessageName , id)).then((doc) => {
        fetchMessageData()
        alert("")
      })
      .catch(() => {
        alert("")
     })
    }else{
      console.log("")
    }
  }

  // 
  const handleClick = () => {
    setError(false)
    const firestore = firebaseApp.firestore

    // 
    if (sendmessage === "") {
        console.log("")
        setError(true)
        return
      }

    try {
        const docRef = collection(firestore, "message")
        addDoc(docRef, {
            text:sendmessage,                           // 
            createdAt: Timestamp.fromDate(new Date()),  // 
            recipeCommentNum : getrecipenum,            // (recipenum)
            user: {
                name: profile?.name,                    // 
                image: profile?.image,                  // 
                uid: profile?.uid,                      // UID
              },
          })
        console.log("")
        // 
        fetchMessageData()
        setSendMessage("")
      } catch (err) {
        console.log("")
        console.log(err)
        setError(true)
      }
  }

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Box sx={{ flexGrow: 1, m: 2, }}>
        <Grid item xs={12}>
          {message ? (
            message.map((message) => (
              <Box 
                key={message.id} 
                sx={{
                  display: "flex",
                  flexDirection:profile && profile.uid === message.user.uid ? "row" : "row-reverse",
                  my: 2,
                  gap: 2,
                  flexGrow: 1, m: 2,}}>
                <Box>
                  <Avatar src={message.user.image ? message.user.image : ""} alt="" />
                </Box>
                <Box sx={{ ml: 0 }}>
                  <Typography 
                    sx={{ fontSize: 12 }}
                    align = {profile && profile.uid === message.user.uid ? "left" : "right"}>
                      {/*  */}
                    {message.user.name}
                  </Typography>
                  <Typography 
                    sx={{ 
                      p: 1, 
                      background: "#dddddd", 
                      borderRadius: 1 ,
                      maxWidth : 400,
                      minWidth : 200,}}
                    align = {profile && profile.uid === message.user.uid ? "left" : "right"}>
                      {/*  */}
                      {message.text}
                  </Typography>
                  <Grid container spacing={0}>
                    <Grid item xs={8} align = "left">
                      <Typography sx={{ fontSize: 12 }}>
                        {/*  */}
                        {format(message.createdAt.toDate(), "yyyyMMddhh:mm")}
                      </Typography>
                    </Grid>
                    <Grid item xs={3} align = "right">
                      <Typography sx={{ fontSize: 12 }}>
                        {/*  */}
                        {time(message.createdAt)}
                      </Typography>
                    </Grid>
                    <Grid item xs={1} align = "center">
                      {/*  */}
                      {/*  */}
                      {/*  */}
                      {profile && (profile.uid === message.user.uid || profile.uid === store.getState().recipeUID)  ? 
                        <IconButton 
                          onClick={() => handleDelete(message.id)}>
                          <Tooltip title="" arrow>
                            <DeleteIcon sx={{ color : "#808080" ,fontSize: 16}}/>
                          </Tooltip>
                        </IconButton> : ""}
                    </Grid>
                  </Grid>
                </Box>
              </Box>
            ))) : (
            <p></p>)}
            {/*  */}
            {/*  */}
            <Stack direction="row" spacing={2} sx={{ margin: "0.5rem 1rem" }}>
              <TextField 
                  size="small" 
                  sx={{ flex: 1 }}
                  value = {sendmessage}
                  label = ""
                  onChange={e => setSendMessage(e.target.value)} />
              <Button 
                  variant="contained" 
                  endIcon={<ChatBubbleIcon />}
                  onClick={() => 
                    {profile ? 
                      handleClick()
                    : history.push("/signup")}}>
              
              </Button>
            </Stack>
          </Grid>
      </Box>
      {/*  */}
      {error && <Alert severity="error"></Alert>}
    </ThemeProvider>
  );
}

export default App;