import React             from 'react';
import CssBaseline       from '@mui/material/CssBaseline';
import Typography        from '@mui/material/Typography';
import Container         from '@mui/material/Container';
import { createTheme,
         ThemeProvider } from '@mui/material/styles';
import Grid              from '@mui/material/Grid';
import { withRouter }    from 'react-router';
import URHeader          from './URHeader';
import store             from '../../store/index';
import URButton          from './URButton';
import { useFileUpload } from "use-file-upload";
import PersonAddIcon     from '@mui/icons-material/PersonAdd';
import ReplyIcon         from '@mui/icons-material/Reply';

const theme = createTheme({
  palette: {
    // 
    primary: {
      main: '#E64545',
      contrastText: '#ffffff',
    },
    // 
    background: {
      default: '#ffffff',
    },

    // 
    text: { primary: '#000000' },
  },
});

function UserRegistration(props) {

  const handleCheck = (e) =>{
    console.log("handleCheck")
  }
  // 
  const [files, selectFiles] = useFileUpload();

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Container maxWidth>
        <URHeader/>
      </Container>
        <Container maxWidth="sm">
          <br/>
          <Typography variant="h4" gutterBottom align='center'>
            
          </Typography>
          <Typography variant="h5" gutterBottom align='center'>
            
          </Typography>
          <Grid container spacing={3}>
            <Grid item xs={12}>

            {/* <Container align='center'>
              <Avatar
                alt="preview"
                src={files?.source || store.getState().photoURL}
                padding ="1em"
                sx={{ width: 150, height: 150 }}/>
              <br/>
            </Container> */}

            <Typography variant="h6">
              
            </Typography>
            <Typography variant="body1">
              {store.getState().displayName}
            </Typography>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="h6">
                
              </Typography>
              <Typography variant="body1">
                {store.getState().location}
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="h6">
                
              </Typography>
              <Typography variant="body1">
                {store.getState().address}
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="h6">
                
              </Typography>
              <Typography variant="body1">
                {store.getState().password1}
              </Typography>
            </Grid>
          </Grid>
          <Typography align='center'>
           <URButton
              id        = "registerChancel"
              text      = ""
              variant   ="outlined"
              link      = "/userregistration"
              sx        = {{ mt: 3, ml: 1 }}
              startIcon = {<ReplyIcon/>}/>
            <URButton
              id        = "registerOk"
              text      = ""
              variant   = "contained"
              link      = "/userregistration/Complete"
              sx        = {{ mt: 3, ml: 1 }}
              startIcon = {<PersonAddIcon/>}/>
          </Typography>
        </Container>
    </ThemeProvider>
  );
}

export default withRouter(UserRegistration);
